pre[data-line] {
	position: relative;
	padding: 1em 0 1em 3em;
}
.line-highlight {
	position: absolute;
	left: 0;
	right: 0;
	padding: inherit 0;
	margin-top: 1em;
	background: hsla(24,20%,50%,.08);
	background: linear-gradient(to right,hsla(24,20%,50%,.1) 70%,hsla(24,20%,50%,0));
	pointer-events: none;
	line-height: inherit;
	white-space: pre;
	&:before {
		content: attr(data-start);
		position: absolute;
		top: .4em;
		left: .6em;
		min-width: 1em;
		padding: 0 .5em;
		background-color: $background-color_1;
		color: $color_9;
		font: bold 65%/1.5 sans-serif;
		text-align: center;
		vertical-align: .3em;
		border-radius: 999px;
		text-shadow: none;
		box-shadow: 0 1px #fff;
	}
}
.line-highlight[data-end] {
	&:after {
		content: attr(data-start);
		position: absolute;
		top: .4em;
		left: .6em;
		min-width: 1em;
		padding: 0 .5em;
		background-color: $background-color_1;
		color: $color_9;
		font: bold 65%/1.5 sans-serif;
		text-align: center;
		vertical-align: .3em;
		border-radius: 999px;
		text-shadow: none;
		box-shadow: 0 1px #fff;
		content: attr(data-end);
		top: auto;
		bottom: .4em;
	}
}
.line-numbers {
	.line-highlight {
		&:after {
			content: none;
		}
		&:before {
			content: none;
		}
	}
	.line-numbers-rows {
		position: absolute;
		pointer-events: none;
		top: 0;
		font-size: 100%;
		left: -3.8em;
		width: 3em;
		letter-spacing: -1px;
		border-right: 1px solid #999;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}
}
pre[id].linkable-line-numbers {
	span.line-numbers-rows {
		pointer-events: all;
		>span {
			&:before {
				cursor: pointer;
			}
			&:hover {
				&:before {
					background-color: $background-color_2;
				}
			}
		}
	}
}
pre[class*=language-].line-numbers {
	position: relative;
	padding-left: 3.8em;
	counter-reset: linenumber;
	>code {
		position: relative;
		white-space: inherit;
	}
}
.line-numbers-rows {
	>span {
		display: block;
		counter-increment: linenumber;
		&:before {
			content: counter(linenumber);
			color: $color_10;
			display: block;
			padding-right: .8em;
			text-align: right;
		}
	}
}
div.code-toolbar {
	position: relative;
	>.toolbar {
		position: absolute;
		z-index: 10;
		top: .3em;
		right: .2em;
		transition: opacity .3s ease-in-out;
		opacity: 0;
		>.toolbar-item {
			display: inline-block;
			>a {
				cursor: pointer;
				color: $color_12;
				font-size: .8em;
				padding: 0 .5em;
				background: #f5f2f0;
				background: rgba(224,224,224,.2);
				box-shadow: 0 2px 0 0 rgba(0,0,0,.2);
				border-radius: .5em;
				&:focus {
					color: $color_11;
					text-decoration: none;
				}
				&:hover {
					color: $color_11;
					text-decoration: none;
				}
			}
			>button {
				background: 0 0;
				border: 0;
				color: $color_11;
				font: inherit;
				line-height: normal;
				overflow: visible;
				padding: 0;
				-webkit-user-select: none;
				-moz-user-select: none;
				-ms-user-select: none;
				color: $color_12;
				font-size: .8em;
				padding: 0 .5em;
				background: #f5f2f0;
				background: rgba(224,224,224,.2);
				box-shadow: 0 2px 0 0 rgba(0,0,0,.2);
				border-radius: .5em;
				&:focus {
					color: $color_11;
					text-decoration: none;
				}
				&:hover {
					color: $color_11;
					text-decoration: none;
				}
			}
			>span {
				color: $color_12;
				font-size: .8em;
				padding: 0 .5em;
				background: #f5f2f0;
				background: rgba(224,224,224,.2);
				box-shadow: 0 2px 0 0 rgba(0,0,0,.2);
				border-radius: .5em;
				&:focus {
					color: $color_11;
					text-decoration: none;
				}
				&:hover {
					color: $color_11;
					text-decoration: none;
				}
			}
		}
	}
	&:hover {
		>.toolbar {
			opacity: 1;
		}
	}
	&:focus-within {
		>.toolbar {
			opacity: 1;
		}
	}
}
.command-line-prompt {
	border-right: 1px solid #999;
	display: block;
	float: left;
	font-size: 100%;
	letter-spacing: -1px;
	margin-right: 1em;
	pointer-events: none;
	text-align: right;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	>span {
		&:before {
			opacity: .7;
			content: ' ';
			display: block;
			padding-right: .8em;
		}
	}
	>span[data-user] {
		&:before {
			content: "[" attr(data-user) "@" attr(data-host) "] $";
		}
	}
	>span[data-user=root] {
		&:before {
			content: "[" attr(data-user) "@" attr(data-host) "] #";
		}
	}
	>span[data-prompt] {
		&:before {
			content: attr(data-prompt);
		}
	}
	>span[data-continuation-prompt] {
		&:before {
			content: attr(data-continuation-prompt);
		}
	}
}
