pre.diff-highlight {
	>code {
		.token.deleted {
			&:not(.prefix) {
				background-color: $background-color_3;
				color: $color_11;
				display: block;
			}
		}
		.token.inserted {
			&:not(.prefix) {
				background-color: $background-color_4;
				color: $color_11;
				display: block;
			}
		}
	}
}
