@font-face {
    font-family: 'icomoon';
    src:
        url('../fonts/icomoon/icomoon.woff2?4fpajq') format('woff2'),
        url('../fonts/icomoon/icomoon.woff?4fpajq') format('woff');
        
        

    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

.ico {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    font-family: 'icomoon';
    font-weight: 500;
}

.icob {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
